import React from 'react';

// libs
import Axios from 'axios';

// VK
import connect from '@vkontakte/vk-connect';
import connectPromise from "@vkontakte/vkui-connect-promise";
import { 
	Avatar,
	Cell,
	ConfigProvider,
	Div,
	Epic,
	Gallery,
	Group,
	List,
	Panel,
	PanelHeader,
	PullToRefresh,
	Spinner,
	Tabbar,
	TabbarItem,
	View
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
// import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';

import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28User from '@vkontakte/icons/dist/28/profile';
import Icon28Users from '@vkontakte/icons/dist/28/users';
//import Map from './components/Map';
import StarRatings from 'react-star-ratings';

// styles
import './styles/App.css';

const APP_NAME = 'На Месте';
const ENABLE_DEBUG = false;

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			// app
			activePanel: 'discover',
			activeStory: 'discover',
			activeView: 'discover',
			appInitTimestamp: 0,
			isFetchingDiscover: false,
			isFetchingFriends: false,
			isFetchingProfile: false,

			// user
			fetchedUser: null,
			isAuthorized: false,

			// userAllowedScopes: false, // TODO

			userData: [],
			userDiscoverFeed: [],
			userFriendsInApp: [],
			userGeodata: null,
			vkAccessToken: null,
		};

		this.onPanelChange = this.onPanelChange.bind(this);
		this.onStoryChange = this.onStoryChange.bind(this);
		this.onViewChange = this.onViewChange.bind(this);
		this.onRefreshDiscover = this.onRefreshDiscover.bind(this);
	}

	componentDidMount() {
		this.setState({
			appInitTimestamp: this.getTimestamp(),
		});

		// VK CONNECT
		connect.subscribe((e) => {
			if (e.detail.hasOwnProperty('type')) {
				switch (e.detail.type) {
					case 'VKWebAppGetUserInfoResult':
						this.debugLog(e.detail.data);

						// save user data
						this.setState({ fetchedUser: e.detail.data });
	
						// auth user
						Axios.get('https://ybsapi.herokuapp.com/user/authorize/' + e.detail.data.id)
							.then(response => {
								this.debugLog('! YBS-API: user/authorize');

								if (response.data.hasOwnProperty('data')) {
									this.debugLog(response.data.data);

									let user_data = response.data.data;
									if (!user_data.hasOwnProperty('level')) {
										user_data.level = 1;
									}

									this.setState({
										isAuthorized: true,
										userData: user_data,
									});
								} else {
									this.debugLog('! Auth: Failed!');
								}
							})
							.catch(error => {
								this.debugLog('ERROR: ' + error);
							});
						break;
					case 'VKWebAppGeodataResult':
						this.debugLog('! Result: Geodata');
						this.debugLog(e.detail.data.lat + ',' + e.detail.data.long);

						this.setState({ 
							userGeodata: {
								lat: e.detail.data.lat,
								lng: e.detail.data.long
							}
						});
						break;
					case 'VKWebAppAccessTokenReceived':
						this.debugLog('! Result: AccessToken');

						if (e.detail.data.hasOwnProperty('access_token')) {
							this.setState({
								vkAccessToken: e.detail.data.access_token
							});
	
							this.getFriends();
							this.getGroups();
						} else {
							this.debugLog('ACCESS_TOKEN_ERROR');
						}
						break;
					case 'VKWebAppCallAPIMethodResult':
						if (e.detail.data.hasOwnProperty('request_id')) {
							switch (e.detail.data.request_id) {
								case '1fr':
									Axios.post('https://ybsapi.herokuapp.com/user/friends/' + this.state.fetchedUser.id, e.detail.data.response.items)
										.then(response => {
											this.debugLog('! YBS-API: user/friends');
											
											if (response.data.hasOwnProperty('data')) {
												this.debugLog(response.data.data);

												this.setState({
													isFetchingFriends: false,
													userFriendsInApp: response.data.data,
												});
											} else {
												this.debugLog(response.data);
											}
										})
										.catch(error => {
											this.debugLog('ERROR: ' + error);
										});
									break;

								case '1gr':
									Axios.post('https://ybsapi.herokuapp.com/user/communities/' + this.state.fetchedUser.id, e.detail.data.response.items)
										.then(response => {
											this.debugLog('! YBS-API: user/communities');
											
											if (response.data.hasOwnProperty('data')) {
												this.debugLog(response.data.data);

												// prepare feed
												let feedItems = [];
												let feedItemsWithPhotos = [];
												let feedItemsWithoutPhotos = [];

												response.data.data.forEach(value => {
													if (value.hasOwnProperty('name') && value.name !== (this.state.fetchedUser.city.title || '')) {
														let splitted_desc = value.description.split(',');
														value['new_name'] = splitted_desc[0];

														if (splitted_desc.length > 3) {
															delete splitted_desc[0];
															let new_desc = splitted_desc.filter(function(e){return e}).join(',').trim();
															value['new_desc'] = new_desc.charAt(0).toUpperCase() + new_desc.slice(1);
														} 

														// posts with photos first
														if (value.hasOwnProperty('photo_url')) {
															feedItemsWithPhotos.push(value);
														} else {
															feedItemsWithoutPhotos.push(value);
														}
													}
												});

												feedItems = feedItemsWithPhotos.concat(feedItemsWithoutPhotos);

												this.setState({
													isFetchingDiscover: false,
													userDiscoverFeed: feedItems,
												});
											} else {
												this.debugLog(response.data);
											}
										})
										.catch(error => {
											this.debugLog('ERROR: ' + error);
										});
									break;
								default:
									this.debugLog(e.detail.data);
							}
						}
						break;
					default:
						this.debugLog('ERROR: ' + e.detail.type);
				}
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		//connect.send('VKWebAppGetGeodata', {});

		this.getToken();
	}

	callAPI = (method, params) => {
		params['access_token'] = this.state.vkAccessToken;
		params['v'] = '5.101';
	
		return connectPromise.send('VKWebAppCallAPIMethod', {
			'method': method,
			'params': params
		}).then(data => {
			return data.data.response;
		}).catch(error => {
			return error;
		});
	};

	debugLog(message) {
		if (!ENABLE_DEBUG) {
			return;
		}

		console.log(message);
	}

	getAppRuntime() {
		return (this.getTimestamp() - this.state.appInitTimestamp);
	}

	getTimestamp() {
		return Math.floor(Date.now() / 1000);
	}

	getFriends() {
		connect.send('VKWebAppCallAPIMethod', {
			'method': 'friends.get',
			'request_id': '1fr',
			'params': {
				'fields': 'city,country,domain,first_name,last_name,photo_100,photo_200',
				'count': 1000,
				'order': 'mobile',
				'access_token': this.state.vkAccessToken,
				'v': '5.101',
			}
		});
	}

	getGroups = () => {
		connect.send('VKWebAppCallAPIMethod', {
			'method': 'groups.get',
			'request_id': '1gr',
			'params': {
				'fields': 'city,country,place,description,members_count,links,site,is_favorite,photo_100,photo_200',
				'count': 1000,
				'extended': true,
				'access_token': this.state.vkAccessToken,
				'v': '5.101',
			}
		});
	}

	getMe = () => {
		this.setState({ isFetchingProfile: true });
	  
		setTimeout(() => {
			this.setState({
				isFetchingProfile: false
			});
		});
	}

	getToken = () => {
		connect.send('VKWebAppGetAuthToken', {'app_id': 7150409, 'scope': 'friends,groups'});
	}

	onPanelChange (e) {
		this.setState({
			activePanel: e.currentTarget.dataset.panel
		});
	}

	onRefreshDiscover = () => {
		this.setState({ isFetchingDiscover: true });
	  
		setTimeout(() => {
			this.getGroups();
		});
	}

	onRefreshFriends = () => {
		this.setState({ isFetchingFriends: true });
	  
		setTimeout(() => {
			this.getFriends();
		});
	}

	onRefreshProfile = () => {
		this.setState({ isFetchingProfile: true });
	  
		setTimeout(() => {
			this.getMe();
		});
	}

	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story });
	}

	onViewChange (e) {
		this.setState({ activeView: e.currentTarget.dataset.view });
	}

	render() {
		return (
			<ConfigProvider isWebView={true}>
				<Epic 
					activeStory={this.state.activeStory} 
					tabbar={
						<Tabbar>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'discover'}
								data-story="discover"
								text="Актуальное"
							><Icon28Newsfeed /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'friends'}
								data-story="friends"
								text="Друзья"
							><Icon28Users /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'profile'}
								data-story="profile"
								text="Профиль"
							><Icon28User /></TabbarItem>
						</Tabbar>
					}
				>
					<View id="discover" activePanel="discover">
						<Panel id="discover">
							<PanelHeader>
								{ APP_NAME }
							</PanelHeader>
							<PullToRefresh onRefresh={this.onRefreshDiscover} isFetching={this.state.isFetchingDiscover}>
								{(this.state.userDiscoverFeed.length === 0 && this.getAppRuntime() <= 10) && 
									<Group style={{paddingTop: '20px'}}>
										<Spinner size="medium" style={{ marginTop: 20 }} />
										<Div style={{textAlign: 'center'}}>
											Мы подбираем интересные места для Вас, это может занять какое-то время
										</Div>
									</Group>
								}

								{(this.state.userDiscoverFeed.length === 0 && this.getAppRuntime() > 10) && 
									<Group style={{
										paddingTop: '20px',
										paddingBottom: '20px'
									}}>
										<Div style={{textAlign: 'center'}}>
											Упс, что-то пошло не так (
										</Div>
									</Group>
								}

								<Group>
									<List>
										{this.state.userDiscoverFeed.map((item, i) => 
											<Group key={i} title={ item.new_name }>
												<Gallery
													slideWidth="100%"
													align="center"
													style={{ height: 200 }}
												>
													{ item.photo_url && 

														<div style={{
															backgroundImage: 'url("' + item.photo_url + '")',
															width: '100%'
														}}/>
													}

													{
														!item.photo_url && 
															<div style={{ backgroundColor: 'var(--accent)' }} />
													}
												</Gallery>
												{	item.rating &&
													<Div>
														<span style={{
															color: '#9a9a9a',
															marginRight: '5px',
														}}>
															{ item.rating }
														</span>
														<StarRatings 
															rating={ item.rating }
															starEmptyColor="white"
															starHoverColor="gold"
															starRatedColor="#ffcc00"
															name="rating"
															starDimension="16px"
															starSpacing="2px"
														/>
														<span style={{
															color: '#9a9a9a',
															marginRight: '5px',
														}}>
															({ item.user_ratings_total || 0 })
														</span>
													</Div>
												}
												<Cell description={ item.address } />
											</Group>
										)}
									</List>
								</Group>
							</PullToRefresh>
						</Panel>
					</View>

					<View id="friends" activePanel="friends">
						<Panel id="friends">
							<PanelHeader>
								{ APP_NAME }
							</PanelHeader>
							<PullToRefresh onRefresh={this.onRefreshFriends} isFetching={this.state.isFetchingFriends}>
								<Group>
									<List>
										{this.state.userFriendsInApp.map((item, i) => 
											<Div key={i}>
												<Cell before={<Avatar src={ item.photo_200 } />}>
													<span style={{
														color: '#3a3a3a',
													}}>
														{ item.first_name + ' ' + item.last_name }
													</span>
													<br />
													<span style={{color: '#9a9a9a'}}>
														1 уровень
													</span>
												</Cell>
											</Div>
										)}
									</List>
								</Group>
							</PullToRefresh>
						</Panel>
					</View>

					<View id="profile" activePanel="profile">
						<Panel id="profile">
							<PanelHeader>
								{ APP_NAME }
							</PanelHeader>
							<PullToRefresh onRefresh={this.onRefreshProfile} isFetching={this.state.isFetchingProfile}>
								{this.state.fetchedUser &&
									<Group>
										<List>
											<Div>
												<Cell
													before={this.state.fetchedUser.photo_200 ? <Avatar src={this.state.fetchedUser.photo_200}/> : <Avatar><Icon28User /></Avatar>}
												>
													<span style={{
														color: '#3a3a3a',
													}}>
														{`${this.state.fetchedUser.first_name} ${this.state.fetchedUser.last_name}`}
													</span>
													<br />
													<span style={{color: '#9a9a9a'}}>
														1 уровень
													</span>

												</Cell>

												
											</Div>
										</List>
									</Group>
								}
							</PullToRefresh>
						</Panel>
					</View>
				</Epic>
			</ConfigProvider>
		);
	}
}

export default App;
